<template>
  <div id="app">
    <!-- 頭部nav -->
    <van-nav-bar
      :title="title"
      fixed
      v-show="navShow" />
    
    <!-- 主體內容 -->
    <!-- 路由規則 組建渲染位置 -->
    <router-view @onTitle="getTitle" @onNavShow="getNavShow"></router-view>

    <!-- 底部tabbar -->
    <van-tabbar v-model="active" active-color="red">
      <van-tabbar-item icon="home-o" to="/home">商城</van-tabbar-item>
      <van-tabbar-item icon="filter-o" to="/assort">分類</van-tabbar-item>
      <van-tabbar-item icon="search" to="/search">搜索</van-tabbar-item>
      <van-tabbar-item icon="cart-o" to="/cart" info="10">購物車</van-tabbar-item>
      <van-tabbar-item icon="contact" to="/mine">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { NavBar, Tabbar, TabbarItem } from 'vant';

export default {
  name: 'app',
  data() {
    return {
      active: 0,
      title: '', // nav title
      navShow: true // nav show or hidden
    }
  },
  methods: {
    getTitle: function(title) {
      this.title = title
    },
    getNavShow: function(show) {
      this.navShow = show
    }
  },
  components: {
    [NavBar.name]: NavBar,
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
  }
}
</script>

